import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { from } from 'rxjs';
import { FileUpComponent } from './file-up/file-up.component';
import { ImportedUploaderComponent } from './imported-uploader/imported-uploader.component';

const routes: Routes = [
  {path: 'file-up', component: FileUpComponent},
  {path: '', component: FileUpComponent, pathMatch: 'full'},
  {path: 'imported-uploader', component: ImportedUploaderComponent}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
